.login-background {
    background-image: url('/public/ueprios.jpg'); /* Asegúrate de que esté en /public */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    width: 100%;
    position: relative;
    overflow: hidden;
  }
  
  .login-background::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(5px); /* aquí se aplica el desenfoque */
    background-color: rgba(42, 41, 41, 0.382); /* ligera capa para mayor contraste */
    z-index: 0;
  }
  
  .login-background > * {
    position: relative;
    z-index: 1;
  }
  